import { createApp } from 'vue';
import axios from 'axios';
import VueAxios from 'vue-axios';
// eslint-disable-next-line import/no-extraneous-dependencies
import {
  Field, Form, ErrorMessage, defineRule, configure,
} from 'vee-validate';
// 引入 VeeValidate 的驗證規則
// eslint-disable-next-line import/no-extraneous-dependencies
import * as rules from '@vee-validate/rules';
// 引入 VeeValidate 的 i18n 功能
// eslint-disable-next-line import/no-extraneous-dependencies
import { localize, setLocale } from '@vee-validate/i18n';
// 引入 VeeValidate 的繁體中文語系檔
// eslint-disable-next-line import/no-extraneous-dependencies
import zhTW from '@vee-validate/i18n/dist/locale/zh_TW.json';
import 'bootstrap';
import VCalendar from 'v-calendar';
import 'v-calendar/style.css';
import mitt from 'mitt';
import { createI18n } from 'vue-i18n';
// 本地語言
import zh from '@/assets/languageText/zh-TW.json';
import en from '@/assets/languageText/en-US.json';
import id from '@/assets/languageText/id-ID.json';
import vi from '@/assets/languageText/vi-VN.json';
import toCurrency from './assets/javascript/toCurrency';
import App from './App.vue';
import router from './router';

// 使用 Object.keys 將 AllRules 轉為陣列並使用 forEach 迴圈將驗證規則加入 VeeValidate
Object.keys(rules).forEach((rule) => {
  if (typeof rules[rule] === 'function') {
    defineRule(rule, rules[rule]);
  }
});

// 將當前 VeeValidate 的語系設定為繁體中文
configure({
  generateMessage: localize({ zh_TW: zhTW }),
  validateOnInput: true,
});
setLocale('zh_TW');

const i18n = createI18n({
  legacy: false,
  globalInjection: true, // 123
  // eslint-disable-next-line func-names
  locale: (function () {
    if (localStorage.getItem('lang')) {
      return localStorage.getItem('lang');
    }
    return 'en-US';
  }()),
  // locale: 'en-US',
  fallbackLocale: 'en-US',
  messages: {
    'zh-TW': zh,
    'en-US': en,
    'id-ID': id,
    'vi-VN': vi,
  },
});

const emitter = mitt();

const app = createApp(App);

app.config.globalProperties.$toCurrency = toCurrency;
app.config.globalProperties.$emitter = emitter;

// 掛載 Global 的 VeeValidate 元件
app.component('VField', Field);
app.component('VForm', Form);
app.component('ErrorMessage', ErrorMessage);

app.use(i18n);
app.use(VCalendar, {});
app.use(VueAxios, axios);
app.use(router);

// 切換網頁時回到最上方
router.afterEach(() => {
  window.scrollTo(0, 0);
});

app.mount('#app');
