<template>
  <section class="row">
    <div class="col d-none d-sm-block border border-dark">
      <div
        class="d-flex flex-column align-items-center justify-content-between h-100 py-3"
      >
        <template v-for="(item, index) in languageList" :key="index">
          <a
            href="#"
            class="text-decoration-none h4 position-relative text-nowrap h5"
            @click.prevent="setLanguage(item)"
            >{{ item.language }}
            <Circle1
              v-if="circleNum === 1"
              :class="{ 'd-none': language !== item.language }"
            />
            <Circle2
              v-else-if="circleNum === 2"
              :class="{ 'd-none': language !== item.language }"
            />
            <Circle3 v-else :class="{ 'd-none': language !== item.language }" />
          </a>
        </template>
      </div>
    </div>
    <div class="col-12 col-sm-7 col-lg-8 border border-dark">
      <div class="my-3">
        <router-link
          to="/"
          class="d-flex flex-column align-items-center text-decoration-none text-dark"
        >
          <h5 class="mb-0">
            {{ today }}, {{ dateMonth.toUpperCase() }} {{ date }},
            {{ dateYears }}
          </h5>
          <h1
            class="font-monospace fw-bolder mb-0 text-nowrap"
            style="letter-spacing: 5px"
          >
            {{ $t('title') }}
          </h1>
          <h4 class="mb-0 font-monospace fw-bold text-uppercase">
            {{ $t('subtitle') }}
          </h4>
        </router-link>
      </div>
    </div>
    <div class="col d-none d-sm-block border border-dark">
      <div
        class="d-flex flex-column justify-content-center align-items-center h-100"
      >
        <i class="bi bi-basket2 h1 position-relative">
          <span
            class="position-absolute top-0 start-100 translate-middle rounded-pill text-secondary"
          >
          </span>
        </i>
      </div>
    </div>
  </section>
</template>

<style lang="scss" scoped>
.circle-1 {
  width: 8rem;
}
.circle-2 {
  width: 8rem;
}
.circle-3 {
  width: 8rem;
}
</style>

<script>
import Circle1 from '@/components/helpers/focus/CircleOne.vue';
import Circle2 from '@/components/helpers/focus/CircleTwo.vue';
import Circle3 from '@/components/helpers/focus/CircleThree.vue';

export default {
  data() {
    return {
      circleNum: 2,
      language: 'ENGLISH',
      languageList: [
        { language: 'ENGLISH', locale: 'en-US' },
        { language: '中文', locale: 'zh-TW' },
        { language: 'Indonesia', locale: 'id-ID' },
        { language: 'Tiếng Việt', locale: 'vi-VN' },
      ],
      week: [
        'SUNDAY',
        'MONDAY',
        'TUESDAY',
        'WEDNESDAY',
        'THURSDAY',
        'FRIDAY',
        'SATURSAY',
      ],
      dateYears: '',
      dateMonth: '',
      date: '',
      today: '',
    };
  },
  components: {
    Circle1,
    Circle2,
    Circle3,
  },
  methods: {
    getDate() {
      const newDate = new Date();
      this.dateYears = newDate.getFullYear();
      this.dateMonth = newDate.toLocaleString('en-US', { month: 'short' });
      this.date = newDate.getDate();
      this.today = newDate.getDay();
      this.week.forEach((item, i) => {
        if (i === this.today) {
          this.today = item;
        }
      });
    },
    setLanguage(data = { language: 'ENGLISH', locale: 'en-US' }) {
      this.circleNum = Math.floor(Math.random() * 3);
      this.language = data.language;
      this.$i18n.locale = data.locale;
    },
  },
  mounted() {
    this.getDate();
    const lang = navigator.language;
    if (lang === 'id-ID') {
      this.setLanguage({ language: 'Indonesia', locale: 'id-ID' });
    } else if (lang === 'zh-TW' || lang === 'zh-CN') {
      this.setLanguage({ language: '中文', locale: 'zh-TW' });
    } else if (lang === 'vi-VN ') {
      this.setLanguage({ language: 'Tiếng Việt', locale: 'vi-VN' });
    } else {
      this.setLanguage({ language: 'ENGLISH', locale: 'en-Us' });
    }
  },
};
</script>
