<template>
  <section class="row text-center">
    <template v-for="(item, index) in pathList" :key="index">
      <div
        class="text-uppercase border border-dark col-6 col-md-3"
        :class="[item.path === '/order' ? 'col-sm-12' : ' col-sm-4']"
      >
        <div class="position-relative my-3">
          <router-link
            :to="item.path"
            class="text-decoration-none text-dark p-2 fw-bold h4"
            @click="random"
          >
            {{ $t(item.name) }}
            <CircleOne
              v-if="CircleNum === 2"
              :class="{
                'd-none':
                  this.$route.path.split('/')[1] !== item.path.split('/')[1],
              }"
            />
            <CircleTwo
              v-else-if="CircleNum === 1"
              :class="{
                'd-none':
                  this.$route.path.split('/')[1] !== item.path.split('/')[1],
              }"
            />
            <CircleThree
              v-else
              :class="{
                'd-none':
                  this.$route.path.split('/')[1] !== item.path.split('/')[1],
              }"
            />
          </router-link>
        </div>
      </div>
    </template>
  </section>
</template>

<style lang="scss" scoped>
.circle-1 {
  width: 9rem;
}
.circle-2 {
  width: 10rem;
}
.circle-3 {
  width: 10rem;
}
</style>

<script>
import CircleOne from '@/components/helpers/focus/CircleOne.vue';
import CircleTwo from '@/components/helpers/focus/CircleTwo.vue';
import CircleThree from '@/components/helpers/focus/CircleThree.vue';

export default {
  data() {
    return {
      CircleNum: 0,
      pathList: [
        { name: 'navbar.home', path: '/' },
        { name: 'navbar.about', path: '/about' },
        { name: 'navbar.shop', path: '/shop' },
        { name: 'navbar.order', path: '/order' },
      ],
    };
  },
  components: {
    CircleOne,
    CircleTwo,
    CircleThree,
  },
  methods: {
    random() {
      this.CircleNum = Math.floor(Math.random() * 3);
    },
  },
  mounted() {
    this.random();
  },
};
</script>
