import { createRouter, createWebHashHistory } from 'vue-router';
import HomeView from '../views/HomeView.vue';

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    children: [
      {
        path: '/',
        name: 'index',
        component: () => import('../views/front/IndexView.vue'),
      },
      {
        path: 'about',
        name: '關於',
        component: () => import('../views/front/AboutView.vue'),
      },
      {
        path: 'shop',
        name: '商店',
        component: () => import('../views/front/ShopView.vue'),
      },
      {
        path: 'order',
        name: 'checkOrder',
        component: () => import('../views/front/OrderView.vue'),
        children: [
          {
            path: ':id',
            name: 'orderId',
            component: () => import('../components/order/OrderInfo.vue'),
          },
        ],
      },
    ],
  },
  {
    path: '/backend',
    name: 'backend',
    component: () => import('../views/DashboardView.vue'),
    children: [
      {
        path: '/backend',
        name: 'login',
        component: () => import('../views/backend/SigninView.vue'),
      },
      {
        path: 'home',
        name: '後台首頁',
        component: () => import('../views/backend/HomeView.vue'),
      },
      {
        path: 'orders',
        name: '後台訂單',
        component: () => import('../views/backend/OrderView.vue'),
      },
      {
        path: 'products',
        name: '後台產品',
        component: () => import('../views/backend/ProductView.vue'),
      },
    ],
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
