<template>
  <section class="row">
    <div class="card border-dark col-sm-10 col-md-6">
      <div class="card-body">
        <h6 class="m-0">Copyright © 2023 LULU BAKE. All rights reserved.</h6>
      </div>
    </div>
    <div class="card border-dark col-3 d-none d-md-block">
      <div class="card-body">
        <h6 class="m-0">SITE BY LULU</h6>
      </div>
    </div>
    <div class="card border-dark col-2 col-md-3 d-none d-sm-block">
      <div class="card-body">
        <h6 class="m-0 text-end luluText">P.{{ page }}</h6>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      page: 1,
    };
  },
  watch: {
    $route() {
      if (this.$route.path.split('/')[1] === 'about') {
        this.page = 4;
      } else if (this.$route.path.split('/')[1] === 'shop') {
        this.page = 7;
      } else if (this.$route.path.split('/')[1] === 'order') {
        this.page = 13;
      } else {
        this.page = 1;
      }
    },
  },
};
</script>
