<template>
  <section class="p-1 p-sm-3 p-md-4">
    <component
      :elevation="elevation"
      class="p-0 p-sm-3 p-md-4 w-100"
      :is="elevation === 5 ? 'wired-card' : 'div'"
      ><router-view
    /></component>
  </section>
  <div class="paperOverlay"></div>
  <LoadingModal />
</template>

<style lang="scss">
@import '@/assets/stylesheets/all';
</style>

<script>
import 'wired-elements';
import LoadingModal from '@/components/layout/LoadingModel.vue';

export default {
  inheritAttrs: false,
  data() {
    return {
      fullWidth: 0,
      elevation: 5,
    };
  },
  components: {
    LoadingModal,
  },
  mounted() {
    this.fullWidth = window.innerWidth; // 動態取得瀏覽器寬度
    window.onresize = () => {
      this.fullWidth = window.innerWidth;
      if (this.fullWidth < 578) {
        this.elevation = 0;
      } else {
        this.elevation = 5;
      }
    };
    if (this.fullWidth < 578) {
      this.elevation = 0;
    } else {
      this.elevation = 5;
    }
  },
};
</script>
